import React, { Fragment } from 'react';
import { Container } from '@material-ui/core';
import Projects from '../Projects/projects';
import Home from '../Home/home';
import Footer from '../Footer/footer';
import Nav from '../Nav/nav';
import ContactContainer from '../Contact/contactContainer';
import SkillsContainer from '../Skills/skillsContainer';

function AppAlt(props) {
    const calc = ()=>{
       let originalWidth = window.innerWidth;
       let extraWidth = (120*originalWidth)/100;
       let final = originalWidth + extraWidth;
       console.log(originalWidth, extraWidth, final);
       return final;
    }
    const width = (window.innerWidth);
    return (
        <Container style={{ zoom: "80%" }} maxWidth={false} disableGutters={true}>
            <Nav />
            <div style={{ maxWidth: calc(), margin: "0 auto"}} >
                <Home />
                <SkillsContainer />
                <Projects />
                <ContactContainer />
                <Footer />
            </div>
        </Container>
    )
}

export default AppAlt;